@media screen and (min-width: 1000px) {
    .container-fluid {
        padding-left: 10%;
        padding-right: 10%;
    }
}

@media screen and (min-width: 1200px) {
    .container-fluid {
        padding-left: 15%;
        padding-right: 15%;
    }
}

@media screen and (min-width: 1500px) {
    .container-fluid {
        padding-left: 20%;
        padding-right: 20%;
    }
}

@media screen and (min-width: 1700px) {
    .container-fluid {
        padding-left: 25%;
        padding-right: 25%;
    }
}

i.fas.fa-calendar-alt {
    color: rgb(163, 31, 196);
}


