.modal-open {
    width: 34%;
    position: fixed;
    top: 7%;
    left: 33%;
    outline: none;
    border-radius: 5px;
    -webkit-animation: modal-open 0.8s;
            animation: modal-open 0.8s;
}

.modal-close {
    width: 34%;
    position: fixed;
    top: 7%;
    left: 33%;
    outline: none;
    border-radius: 5px;
    -webkit-animation: modal-close 0.4s;
            animation: modal-close 0.4s;
}

@-webkit-keyframes modal-open {
    from {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes modal-open {
    from {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@-webkit-keyframes modal-close {
    from {
        opacity: 1;
    }
    to {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
}

@keyframes modal-close {
    from {
        opacity: 1;
    }
    to {
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
}

.my-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #858482;
}


